	var that;
    export default {
        data() {
            return {
                msgContents: ["界面显示错乱", "启动缓慢，卡出翔了", "UI无法直视，丑哭了", "偶发性崩溃"],
                imageList: [],
				 strimageList: [],
                sendDate: {
                    content: ""
                }
            }
        },
        onLoad() {
          that=  this;
        },
        methods: {
            close(e){
                that.imageList.splice(e,1);
            },
            chooseMsg() { //快速输入
                uni.showActionSheet({
                    itemList: that.msgContents,
                    success: (res) => {
                        that.sendDate.content = that.msgContents[res.tapIndex];
                    }
                })
            },
            chooseImg() { //选择图片
                uni.chooseImage({
                    sourceType: ["camera", "album"],
                    sizeType: "compressed",
                    count: 3 - that.imageList.length,
                    success: (res) => {
                        that.imageList = that.imageList.concat(res.tempFilePaths);
					
						that.strimageList =[];
							for (var i = 0; i < that.imageList.length; i++) {
								uni.uploadFile({
								   url: that.$api_url + '/file/image_upload',
								   filePath: that.imageList[i],
								   name: 'upload_file',
								   header: {
								   	'Content-Type': 'application/x-www-form-urlencoded',
								   	'authorization': uni.getStorageSync('authorization') //自定义请求头信息
								   },
								   success: (res) => {
									   var json=JSON.parse(res.data)
								       if (json.code == 1000) {
										   console.log(json);
										   that.strimageList = that.strimageList.concat(json.content.url);
								       }
								   },
								    fail: (res) => {
								        console.log(res)
								    }
								});
							}
                    }
                })
            },
           
            previewImage(index) { //预览图片
                uni.previewImage({
                    urls: that.imageList,
					current:that.imageList[index]
                });
            },
            send() { //发送反馈
            
				if(""==that.sendDate.content){
					uni.showToast({
						title:'请输入意见'
					})
					return
				}
				uni.showLoading({
					title:'数据提交中...'
				})
				
               uni.request({
                   url: that.$api_url+'/user/feedback',
                  method: 'POST',
				  header:{
					  'Content-Type':'application/x-www-form-urlencoded',
				  	'authorization': uni.getStorageSync('authorization') //自定义请求头信息
				  },
                  data: {
                  	opinion: that.sendDate.content,
                  	imageUrls: that.strimageList
                  },
                 
                   success: (res) => {
					   console.log(res)
					   uni.hideLoading();
                       if (res.data.code == 1000) {
                           uni.showToast({
                               title: "反馈成功!"
                           });
                          
						   setTimeout(function () {
							   uni.navigateBack({
							       delta: 1
							   });
						   }, 1000);
                       }
                   },
                    fail: (res) => {
                        console.log(res)
						 uni.hideLoading();
                    }
                });
            }
        }
    }
